import React, { FunctionComponent } from 'react'
import { MarkdownRemark, Site } from '@/graphql-types'
import { graphql, Link } from 'gatsby'
import { GiTalk } from '../components/gitalk'
import { useSpring, animated } from 'react-spring'
import classnames from 'classnames'
import Layout from '../components/layout/layout'
import { SeriesTab } from '../components/series-tab'

const Styles = require('../styles/templates/series-post.module.styl')

type Props = {
  data: {
    markdownRemark: MarkdownRemark
    site: Site
  }
  location: Location
  pageContext: {
    slug: null | string
    previous: null | MarkdownRemark
    next: null | MarkdownRemark
  }
}

const SeriesPostTemplate: FunctionComponent<Props> = props => {
  const post = props.data.markdownRemark
  const siteTitle = props.data.site.siteMetadata.title // graphql 查询的数据
  // 由 createPage context 传递过来的数据
  const { previous, next } = props.pageContext

  const spring = useSpring({ opacity: 1, transform: 'translate3d(0,0,0)', from: { opacity: 0, transform: 'translate3d(0, -50px, 0)' } })
  return (
    <Layout
      contentStyle={{ width: '100vw', display: 'flex', paddingLeft: '320px' }}
      post={post}
      location={props.location}
      title={siteTitle}
    >
      <SeriesTab slug={props.pageContext.slug} />
      <animated.div style={spring}>
        <div className={Styles['main']}>
          <article className={Styles['article']}>
            <header>
              <h1 className={Styles['title']}>{post.frontmatter.title}</h1>
              <p className={Styles['date']}>{post.frontmatter.date}</p>
            </header>
            <section className='post-detail-container' dangerouslySetInnerHTML={{ __html: post.html }} />
          </article>
          <div className={classnames(Styles['bottom'], { [Styles['havetwo']]: previous && next })}>
            {previous && (
              <Link className={Styles['pre']} to={`/series${previous.fields.slug}`} rel='prev'>
                <i className={classnames(Styles['icon'], 'iconfont', Styles['left'])}>&#xe65d;</i>{' '}
                <span className={Styles['title']}>{previous.frontmatter.title}</span>
              </Link>
            )}
            {next && (
              <Link className={Styles['next']} to={`/series${next.fields.slug}`} rel='next'>
                <span className={Styles['title']}>{next.frontmatter.title}</span>
                <i className={classnames('iconfont', Styles['icon'])}>&#xe65d;</i>
              </Link>
            )}
          </div>
          <GiTalk postId={new Date(post.frontmatter.date).getTime().toString()} />
        </div>
      </animated.div>
    </Layout>
  )
}

export default SeriesPostTemplate

export const SeriesQuery = graphql`
  query SeriesQueryBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
      }
    }
  }
`
