import React, { FunctionComponent, useState, useMemo } from 'react'
import { Menu, Icon } from 'antd'
import { MarkdownRemark, MarkdownRemarkConnection } from '@/graphql-types'
import { graphql, useStaticQuery, Link } from 'gatsby'
import { isArray } from 'util'

const Styles = require('./series-tab.comonent.module.styl')

const { SubMenu } = Menu

const categoryMap = new Map([['she-ji-mo-shi-yuan-ze', '设计模式原则']])

type Props = {
  slug: string
}

type SeriesProps = {
  allMarkdownRemark: MarkdownRemarkConnection
}

export const SeriesTab: FunctionComponent<Props> = props => {
  const currentSeries = props.slug.split('/')[1]

  const data = useStaticQuery<SeriesProps>(
    graphql`
      {
        allMarkdownRemark(sort: { fields: [frontmatter___date], order: ASC }, filter: { fileAbsolutePath: { regex: "/series/" } }) {
          edges {
            node {
              fileAbsolutePath
              fields {
                slug
              }
              frontmatter {
                title
                date(formatString: "YYYY-MM-DD")
              }
            }
          }
        }
      }
    `,
  )

  const currentSeriesPosts = data.allMarkdownRemark.edges.filter(edge => edge.node.fields.slug.indexOf(currentSeries) !== -1)
  // 目前先支持一个层级 [todo: 支持多个层级]
  const seriesPostsBySlug = currentSeriesPosts.reduce((pre, cur) => {
    const slugSplits = cur.node.fields.slug.split('/').filter(c => c)
    // 有文件夹
    if (slugSplits.length === 3) {
      const category = slugSplits[1]
      if (pre.some(post => Object.keys(post)[0] === category)) {
        const index = pre.findIndex(post => Object.keys(post)[0] === category)
        pre[index][category].push(cur)
      } else {
        const obj = { [category]: [cur] }
        pre.push(obj)
      }
    } else {
      pre.push(cur)
    }

    return pre
  }, [])

  const selectedKeys = useMemo(() => currentSeriesPosts.find(s => s.node.fields.slug === props.slug).node.fields.slug, [currentSeriesPosts])

  return (
    <div className={Styles['menu']}>
      <Menu selectedKeys={[selectedKeys]} style={{ width: 320, minHeight: '100%', paddingTop: '10px' }} mode='inline'>
        {seriesPostsBySlug.map((p, index) => {
          return Object.keys(p)[0] !== 'node' ? (
            <SubMenu key={index} title={<a>{categoryMap.get(Object.keys(p)[0])}</a>}>
              {p[Object.keys(p)[0]].map(l => (
                <Menu.Item key={l.node.fields.slug}>
                  <Link to={`/series${l.node.fields.slug}`}>{l.node.frontmatter.title}</Link>
                </Menu.Item>
              ))}
            </SubMenu>
          ) : (
            <Menu.Item key={p.node.fields.slug}>
              <Link to={`/series${p.node.fields.slug}`}>{p.node.frontmatter.title}</Link>
            </Menu.Item>
          )
        })}
      </Menu>
    </div>
  )
}
